/*
 *  Featured Content 5
 *    Styles for the featured content 5 section.
 *
 *  Version:
 *    1.0.0 - 2021/11/11
 */

.c-featured-content-5 {
  background-color: $s-color--background-focus;

  &__outer {
    max-width: $s-page-wrap--xlarge;
    margin: 0 auto;
  }

  &__inner {
    padding: 1.5625rem $s-gutter--edge-mobile 2.375rem;
  }

  &__image-wrapper {
    width: 65.4%;
    margin: 0 auto;
  }

  &__image-container {
    width: 100%;
    padding-bottom: 125.57%;
  }

  &__title {
    margin-top: 2.8125rem;
    margin-bottom: 1.4375rem;
  }

  &__desc {
    margin-left: 4.5625rem;
    max-width: 25.625rem;
  }


  /*** Media Queries ***/
  @include t-media-query($s-small-up) {

    &__inner{
      padding: 3rem $s-gutter--edge-mobile;
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 2rem;
    }

    &__image-wrapper{
      width: 52.6%;
      margin: 0;
      max-width: 21.875rem;
      margin: 0 auto;
    }

    &__content-container{
      max-width: 35rem;
      width: 50%;
    }

    &__image-container{
      width: 100%;
    }

    &__illustration-background {
      position: absolute;
      bottom: -1.5625rem;
      left: 92%;
    }

    &__title{
      margin: 0;
    }
  }

  @include t-media-query($s-medium-up) {

    &__inner {
      position: relative;
      padding: 6rem $s-gutter--edge-medium;
      justify-content: space-around;
      display: grid;
      grid-gap: 7%;
      grid-template-columns: 35% 44%;
    }

    &__image-wrapper{
      width: 100%;
      max-width: initial;
    }

    &__content-container{
      width: 100%;
      margin: 1rem auto 0;
    }

    &__title{
      margin-bottom: 2.5rem;
    }

    &__cta-container{
      margin-top: 3.75rem;
    } 
  }

  @include t-media-query($s-xlarge-up) {
    &__inner {
      padding: 2.75rem $s-gutter--edge-medium 12.625rem;
      justify-content: end;
    }
  }
}